.error-404 {
  border: 1px solid #2d2e2f;
  padding-top: 150px;
  padding-bottom: 150px;
  margin: 0;
  margin-top: 100px;
  margin-bottom: 20px;
  background-color: #2d2e2f99;
  text-align: center;
  -webkit-box-shadow: 0 0 20px rgba(0,0,0,0.7);
          box-shadow: 0 0 20px rgba(0,0,0,0.7);
}

.title-404 {
  font-size: 200px;
  font-weight: bold;
  color: #55bb55cc;
  text-shadow: 0 0 20px rgba(0,0,0,.7);
}

.subtitle-404 {
  font-size: 40px;
  font-weight: bold;
  color: #eee;
  text-shadow: 0 0 20px rgba(0,0,0,.7);
}

@media screen and (max-width: 767px) {
  .title-404 {
    font-size: 100px;
  }
  .subtitle-404 {
    font-size: 20px;
  }
}


