html { 
  overflow-y: scroll; 
}

body {
  /*background:#111 url(/assets/background.jpg) no-repeat top center;*/
  background-color: #111;
  font-family:'Roboto Condensed',sans-serif;
  color:#fff;
  line-height:1;
  overflow-x:hidden
}

.main-container {
  padding:15px;
  background:#161616;
  margin-bottom:100px;
  -webkit-box-shadow:0 0 6px rgba(0,0,0,0.45);
          box-shadow:0 0 6px rgba(0,0,0,0.45)
}

.navbar-collapse {
  padding-left: 0;
}

.navbar-right {
  padding-right: 15px;
}

.brand-icon {
  height: 25px;
  width: 25px;
  display: inline-block !important;
  margin-right: 20px;}

.navbar-brand {
  font-weight: bold;
  display: none;
}

.navbar-nav > li a {
  color:#fff;
  font-size:14px;
  font-weight:900;
  text-transform:uppercase;
  line-height:50px;
  padding:0 18px;
  text-shadow:1px 1px 3px rgba(0,0,0,0.1);
  outline:none;
  -webkit-transition:all .3s ease-in-out;
  -o-transition:all .3s ease-in-out;
  transition:all .3s ease-in-out;
  letter-spacing:.3px;
  border-bottom:3px solid transparent
}

a {
  color:#5b5;
  font-weight: 900;
  outline:0!important;
  text-decoration:none!important;
  -webkit-transition:all .15s ease-out;
  -o-transition:all .15s ease-out;
  transition:all .15s ease-out
}

a:hover {
  color:#5b5!important;
  text-decoration:none!important;
}
  
.navbar-nav > li {
  border-right:1px solid #121212;
}

li.active a {
  background:rgba(0,0,0,0.02)!important;
  border-bottom:3px solid #5b5;
  color:#5b5!important;
}

li a:focus, li a:hover {
  background:rgba(255,255,255,0.02)!important;
  border-bottom:3px solid #5b5;
  color:#5b5!important;
}

.wowprogress:hover > a, .wowprogress a:focus {
  background:rgba(255,255,255,0.02)!important;
  border-bottom:3px solid #27b3c6;
  color:#27b3c6!important;
}

.warcraftlogs:hover > a, .warcraftlogs a:focus {
  background:rgba(255,255,255,0.02)!important;
  border-bottom:3px solid #f18c2f;
  color:#f18c2f!important;
}

.discord:hover > a, .discord a:focus {
  background:rgba(255,255,255,0.02)!important;
  border-bottom:3px solid #7289da;
  color:#7289da!important;
  /*border-bottom:3px solid #3b5998;
  color:#3b5998!important*/
}

.youtube:hover > a, .youtube a:focus {
  background:rgba(255,255,255,0.02)!important;
  border-bottom:3px solid #b00;
  color:#b00!important;
}

.twitch:hover > a, .twitch a:focus, .twitch.active > a {
  background:rgba(255,255,255,0.02)!important;
  border-bottom:3px solid #8462d5;
  color:#a07cf5!important;
}

.left {
  border-left:1px solid #121212;
}

@media screen and (max-width: 1200px) and (min-width: 768px) {
  .wowprogress {display: none !important;}
  .warcraftlogs {display: none !important;}
  .discord {display: none !important;}
  .youtube {display: none !important;}
  .navbar-right {float: left !important;}
}

@media screen and (max-width: 760px) {
  .wowprogress {display: block !important;}
  .warcraftlogs {display: block !important;}
  .discord {display: block !important;}
  .youtube {display: block !important;}
}

@media screen and (max-width: 766px) {
  ul.nav {margin: 0;}
  .navbar-collapse{padding: 0;}
  .navbar-right{padding: 0;}
  .navbar-brand {display: block !important;}
}


