.recruitment {
  border: 1px solid #2d2e2f;
	padding: 0;
  margin: 0;
  margin-bottom: 20px;
  background-color: #2d2e2f99;
  -webkit-box-shadow: 0 0 20px rgba(0,0,0,0.7);
          box-shadow: 0 0 20px rgba(0,0,0,0.7);
}

.recruitment > h2 {
  font-size: 20px;
  color: #fff;
  margin: 20px;
  text-shadow: 0 0 10px rgba(0,0,0,.9);
}

.recruitment-body {
  padding: 20px;
  border-top: 1px solid #5b5;
  border-bottom: 1px solid #5b5;
}

.recruitment-item {
  font-size: 15px;
  padding-top: 5px;
  padding-bottom: 5px;
  text-shadow: 0 0 5px rgba(0,0,0,.9);
}

.priority {
  float: right;
  text-transform: lowercase;
  font-style: italic;
}

.prioLow {color: #7AC491;}

.prioMedium {color: #FFD320;}

.prioHigh {color: #FF420E;}

.deathknight {color: #cc0000;}

.demonhunter {color: #a330c9;}

.druid {color: #ff7d0a;}

.hunter {color: #abd473;}

.mage {color: #69ccf0;}

.monk {color: #00ff96;}

.paladin {color: #f58cba;}

.priest {color: #ffffff;}

.rogue {color: #fff569;}

.shaman {color: #0070de;}

.warlock {color: #9482c9;}

.warrior {color: #c79c6e;}

.recruitment-footer a > .btn, .recruitment-footer a > .btn:active, .recruitment-footer a > .btn:focus {
  color: #fff!important;
  border-radius: 0 !important;
  background-color: #2d2e2f !important;
  text-shadow: 0 0 10px rgba(0,0,0,.7);
}

.recruitment-footer a > .btn:hover {
  color: #fff!important;
  border-radius: 0 !important;
  background-color: #5b5 !important;
  text-shadow: 0 0 10px rgba(0,0,0,.7);
}


