.media-jumbotron {
  background: url(/brigade-app/assets/banner-home.png) no-repeat center center;
  background-size: cover;
  color: #fff!important;
  border-radius: 0 !important;
  margin-top: 75px;
  margin-bottom: 20px;
  padding: 0px!important;
  background-color: #2d2e2f !important;
  height: 200px;
  line-height: 200px;
  -webkit-box-shadow: 0 0 30px rgba(0,0,0,0.7);
          box-shadow: 0 0 30px rgba(0,0,0,0.7);
}

.container > span > h1 {
  font-family: Helvetica, sans-serif;
  font-size: 40px;
  margin-bottom: 10px;
  color: #EEE;
  text-shadow: 0 0 20px rgba(0,0,0,0.9);
}

.container > span > h3 {
  font-family: Helvetica, sans-serif;
  font-size: 20px;
  margin-top: 0px;
  color: 	#EEE;
  text-shadow: 0 0 20px rgba(0,0,0,.9);
}

.container > span {
  display: inline-block;
  vertical-align: middle;
}

.container-fluid {
  margin: 0;
  padding: 0;
}

.media {
  border: 1px solid #2d2e2f;
	padding: 0;
  margin: 0;
  margin-bottom: 20px;
  padding-bottom: 50px;
  background-color: #2d2e2f99;
  -webkit-box-shadow: 0 0 20px rgba(0,0,0,0.7);
          box-shadow: 0 0 20px rgba(0,0,0,0.7);
}

.media > h2 {
  font-size: 20px;
  color: #fff;
  margin: 20px;
  text-shadow: 0 0 10px rgba(0,0,0,.9);
}

.media-body {
  padding: 20px;
  border-top: 1px solid #5b5;
}

.media-body > h2 {
  font-size: 20px;
}

.media-body > .row > .col-sm-4 {
  padding: 10px;
}

hr {
  margin-top: 40px;
  margin-bottom: 40px;
}

.media-title > h5 {
  margin-left: 5px;
  text-shadow: 0 0 10px rgba(0,0,0,.9);
  font-size: 20px;
}

.mediabox {
  margin-top: 20px;
  height: 254.5px;
  width: 100%;
  border: 1px solid #444;
  -webkit-box-shadow: 0 0 15px rgba(0,0,0,.5);
          box-shadow: 0 0 15px rgba(0,0,0,.5);
}

.mediabox > img {
  height: 100%;
  width: 100%;
}

.tier-title {
  font-size: 25px!important;
  text-shadow: 0 0 30px rgba(0,0,0,.9);
}


