.streams-jumbotron {
  background: url(/brigade-app/assets/banner-streams.png) no-repeat center center;
  background-size: cover;
  color: #fff!important;
  border-radius: 0 !important;
  margin-top: 75px;
  margin-bottom: 20px;
  padding: 0px!important;
  background-color: #2d2e2f !important;
  height: 200px;
  line-height: 200px;
  -webkit-box-shadow: 0 0 30px rgba(0,0,0,0.7);
          box-shadow: 0 0 30px rgba(0,0,0,0.7);
}

.container > span > h1 {
  font-family: Helvetica, sans-serif;
  font-size: 40px;
  margin-bottom: 10px;
  color: #EEE;
  text-shadow: 0 0 20px rgba(0,0,0,.9);
}
  
.container > span > h3 {
  font-family: Helvetica, sans-serif;
  font-size: 20px;
  margin-top: 0px;
  color: 	#CCC;
  text-shadow: 0 0 20px rgba(0,0,0,.9);
}
  
.container > span {
  display: inline-block;
  vertical-align: middle;
}
  
.container-fluid {
  margin: 0;
  padding: 0;
}
  
.streams {
  border: 1px solid #2d2e2f;
  padding: 0;
  margin: 0;
  margin-bottom: 20px;
  padding-bottom: 50px;
  background-color: #2d2e2f99;
  -webkit-box-shadow: 0 0 20px rgba(0,0,0,0.7);
          box-shadow: 0 0 20px rgba(0,0,0,0.7);
}
  
.streams > h2 {
  font-size: 20px;
  color: #fff;
  margin: 20px;
  text-shadow: 0 0 10px rgba(0,0,0,.9);
}
  
.streams-body {
  padding: 20px;
  border-top: 1px solid #5b5;
}

.streams-title {
  margin-top: 10px;
  margin-left: 0;
  margin-right: 0;
}

.streams-body > .row > .col-md-4 {
  padding: 10px;
}

.streams-title img {
  float: left;
  height: 30px;
  width: 30px;
  margin-top: -2px;
  margin-left: 5px;
  margin-right: 10px;
  border: 2px solid;
  border-radius: 99px;
  -webkit-box-shadow: 0 0 5px rgba(0,0,0,.5);
          box-shadow: 0 0 5px rgba(0,0,0,.5);
}

.streams-title h5 {
  text-shadow: 0 0 10px rgba(0,0,0,.9);
  font-size: 20px;
  text-transform: capitalize;
}

.overlay {
  background: -webkit-linear-gradient(top, rgba(0,0,0,0) 0%,rgba(0,0,0,.6) 100%,rgba(0,0,0,.6) 100%);
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0,0,0,0)),color-stop(100%, rgba(0,0,0,.6)),to(rgba(0,0,0,.6)));
  background: -o-linear-gradient(top, rgba(0,0,0,0) 0%,rgba(0,0,0,.6) 100%,rgba(0,0,0,.6) 100%);
  background: linear-gradient(to bottom, rgba(0,0,0,0) 0%,rgba(0,0,0,.6) 100%,rgba(0,0,0,.6) 100%);
  position:absolute; 
  top:60px; 
  left:12px; 
  right:12px; 
  bottom: 12px; 
  opacity:0.6;
}

.overlay:hover {
  opacity: 0;
}

.overlay-live {
  background: -webkit-linear-gradient(top, rgba(0,0,0,0) 0%,rgba(20,80,255,.5) 100%,rgba(20,80,255,.5) 100%);
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0,0,0,0)),color-stop(100%, rgba(20,80,255,.5)),to(rgba(20,80,255,.5)));
  background: -o-linear-gradient(top, rgba(0,0,0,0) 0%,rgba(20,80,255,.5) 100%,rgba(20,80,255,.5) 100%);
  background: linear-gradient(to bottom, rgba(0,0,0,0) 0%,rgba(20,80,255,.5) 100%,rgba(20,80,255,.5) 100%);
  position: absolute;
  top: 60px;
  left: 12px;
  right: 12px;
  bottom: 12px;
  opacity: 0.3;
}

.overlay-live:hover {
  opacity: 0;
}

.streambox {
  background: #1d1e1f;
  margin-top: 20px;
  height: 254.5px;
  width: 100%;
  border: 1px solid #444;
  -webkit-box-shadow: 0 0 15px rgba(0,0,0,.5);
          box-shadow: 0 0 15px rgba(0,0,0,.5);
}

.streambox.live {
  border: 1px solid #55f;
}

.streambox > img {
  height: 100%;
  width: 100%;
}

.deathknight {color: #cc0000;}

.demonhunter {color: #a330c9;}

.druid {color: #ff7d0a;}

.hunter {color: #abd473;}

.mage {color: #69ccf0;}

.monk {color: #00ff96;}

.paladin {color: #f58cba;}

.priest {color: #ffffff;}

.rogue {color: #fff569;}

.shaman {color: #0070de;}

.warlock {color: #9482c9;}

.warrior {color: #c79c6e;}


