.home-jumbotron {
  background: url(/brigade-app/assets/banner-home.png) no-repeat center center;
  background-size: cover;
  color: #fff!important;
  border-radius: 0 !important;
  margin-top: 75px;
  margin-bottom: 20px;
  padding: 0px!important;
  background-color: #2d2e2f !important;
  height: 200px;
  line-height: 200px;
  -webkit-box-shadow: 0 0 30px rgba(0,0,0,0.7);
          box-shadow: 0 0 30px rgba(0,0,0,0.7);
  /*box-shadow: 0 20px 30px rgba(0,0,0,0.7); <--- To push box shadow down in case we want to reduce margin between navbar and banner */
}

.container > span > h1 {
  font-family: Helvetica, sans-serif;
  font-size: 40px;
  margin-bottom: 10px;
  color: #EEE;
  text-shadow: 0 0 20px rgba(0,0,0,.9);
}

.container > span > h3 {
  font-family: Helvetica, sans-serif;
  font-size: 20px;
  margin-top: 0px;
  color: 	#CCC;
  text-shadow: 0 0 20px rgba(0,0,0,.9);
}

.container > span {
  display: inline-block;
  vertical-align: middle;
}

.container-fluid {
  margin: 0;
  padding: 0;
}

.killshots {
  border: 1px solid #2d2e2f;
	padding: 0;
  margin: 0;
  margin-bottom: 20px;
  background-color: #2d2e2f99;
  -webkit-box-shadow: 0 0 20px rgba(0,0,0,0.7);
          box-shadow: 0 0 20px rgba(0,0,0,0.7);
}

.killshots > h2 {
  font-size: 20px;
  color: #fff;
  margin: 20px;
  text-shadow: 0 0 10px rgba(0,0,0,.9);
}

.killshots .medialink {
  position: absolute;
  top: 35px;
  right: 30px;
  font-size: 14px;
  color: #ccc;
  float: right;
  text-shadow: 0 0 10px rgba(0,0,0,0.9);
}

.killshots .medialink:hover {
  color: #5b5;
  text-decoration: underline;
}

.killshots-body {
  border-top: 1px solid #5b5;
}

/*.carousel {
  border: 1px solid #2d2e2f;
}*/

.img-responsive,
.thumbnail > img,
.thumbnail a > img,
.carousel-inner > .item > img,
.carousel-inner > .item > a > img {
  display: block;
  width: 100%;
  height: auto;
}

.carousel-inner > .item > img, .carousel-inner > .item > a > img {
  width: 100%;
  height: 100%;
}

/* Since positioning the image, we need to help out the caption */
.carousel-caption {
  z-index: 10;
}


/* Declare heights because of positioning of img element */

.carousel .item {
  height: 498px;
  background-color: #777;
}
.carousel-inner > .item > img {
  position: absolute;
  top: 0;
  left: 0;
  min-width: 40%;
  height: 500px;
}

.carousel-indicators {
  margin-bottom: -10px;
}

.carousel-indicators li {
  border-color: #000000;
  background-color: rgba(70,70,70,.50);
  -webkit-box-shadow: 0 0 30px rgba(0,0,0,0.7);
          box-shadow: 0 0 30px rgba(0,0,0,0.7);
}

.carousel-indicators .active {
  background-color: #5b5;
}

@media screen and (max-width: 800px){
  .carousel .item {
    height: 373px
  }
  .carousel-inner > .item > img {
    height: 375px;
  }
}

@media screen and (max-width: 600px){
  .carousel .item {
    height: 248px
  }
  .carousel-inner > .item > img {
    height: 250px;
  }
}


