.apply-jumbotron {
  background: url(/brigade-app/assets/banner-apply.png) no-repeat center center;
  background-size: cover;
  color: #fff!important;
  border-radius: 0 !important;
  margin-top: 75px;
  margin-bottom: 20px;
  padding: 0px!important;
  background-color: #2d2e2f !important;
  height: 200px;
  line-height: 200px;
  -webkit-box-shadow: 0 0 30px rgba(0,0,0,0.7);
          box-shadow: 0 0 30px rgba(0,0,0,0.7);
}

.container > span > h1 {
  font-family: Helvetica, sans-serif;
  font-size: 40px;
  margin-bottom: 10px;
  color: #EEE;
  text-shadow: 0 0 20px rgba(0,0,0,0.9);
}

.container > span > h3 {
  font-family: Helvetica, sans-serif;
  font-size: 20px;
  margin-top: 0px;
  color: 	#EEE;
  text-shadow: 0 0 20px rgba(0,0,0,.9);
}

.container > span {
  display: inline-block;
  vertical-align: middle;
}

.container-fluid {
  margin: 0;
  padding: 0;
}

.apply {
  border: 1px solid #2d2e2f;
	padding: 0;
  margin: 0;
  margin-bottom: 20px;
  padding-bottom: 50px;
  background-color: #2d2e2f99;
  -webkit-box-shadow: 0 0 20px rgba(0,0,0,0.7);
          box-shadow: 0 0 20px rgba(0,0,0,0.7);
}

.apply > h2 {
  font-size: 20px;
  color: #fff;
  margin: 20px;
  text-shadow: 0 0 10px rgba(0,0,0,.9);
}

.apply-body {
  padding: 20px;
  border-top: 1px solid #5b5;
}

.apply-body > h2 {
  font-size: 20px;
}

hr {
  margin-top: 40px;
  margin-bottom: 40px;
}

.legendary {color: 	#FF8000;}

.epic {color: #A335EE;}

.blue {color: #0070DD;}

.apply-bold {
  font-size: 15px;
  font-weight: bold;
  text-shadow: 0 0 10px rgba(0,0,0,.9);
}

.apply-important {
  color:#ff6969;  
  margin-top: 10px;
  font-weight: bold;
  font-size: 15px;
  text-shadow: 0 0 20px rgba(0,0,0,.9);
}

.apply-requirement {
  margin-top: 15px;
  margin-bottom: 15px;
}

.apply-raidtime {
  margin-top: 15px;
  margin-bottom: 15px;
}

.joinbtn, .joinbtn:active, .joinbtn:focus {
  margin-top: 100px;
  color: #fff!important;
  border-radius: 3px !important;
  background-color: #2d2e2f !important;
  -webkit-box-shadow: 0 0 25px rgba(0,0,0,0.5);
          box-shadow: 0 0 25px rgba(0,0,0,0.5);
  text-shadow: 0 0 10px rgba(0,0,0,.7);
}

.joinbtn:hover {
  color: #fff!important;
  border-radius: 3px !important;
  background-color: #5b5 !important;
}


