footer {
  background-color: #2d2e2f99;
  padding-top: 20px;
  margin-top: 50px;
  -webkit-box-shadow: 0 0 20px rgba(0,0,0,0.7);
          box-shadow: 0 0 20px rgba(0,0,0,0.7);
  border-top: 1px solid #5b5;
}

#footer-row {
  margin-left: 0;
  margin-right: 0;
}

.footer-navigation:hover > a, .footer-navigation:focus > a:focus, 
.footer-navigation:active > a:active, .footer-navigation > a:focus, 
.footer-navigation > a:active, .footer-navigation > a:hover {
  border-bottom: none !important;
}

.footer-navigation-streams:hover > a, .footer-navigation-streams:focus > a:focus, 
.footer-navigation-streams:active > a:active, .footer-navigation-streams > a:focus, 
.footer-navigation-streams > a:active, .footer-navigation-streams > a:hover {
  border-bottom: none !important;
  color:#a07cf5!important;
}

.footer-navigation-wowprogress:hover > a, .footer-navigation-wowprogress:focus > a:focus, 
.footer-navigation-wowprogress:active > a:active, .footer-navigation-wowprogress > a:focus, 
.footer-navigation-wowprogress > a:active, .footer-navigation-wowprogress > a:hover, 
.footer-navigation-raiderio:hover > a, .footer-navigation-raiderio:focus > a:focus,
.footer-navigation-raiderio:active > a:active, .footer-navigation-raiderio > a:focus,
.footer-navigation-raiderio > a:active, .footer-navigation-raiderio > a:hover {
  border-bottom: none !important;
  color:#27b3c6!important;
}

.footer-navigation-warcraftlogs:hover > a, .footer-navigation-warcraftlogs:focus > a:focus,
.footer-navigation-warcraftlogs:active > a:active, .footer-navigation-warcraftlogs > a:focus,
.footer-navigation-warcraftlogs > a:active, .footer-navigation-warcraftlogs > a:hover {
  border-bottom: none !important;
  color:#f18c2f!important;
}

.footer-navigation-discord:hover > a, .footer-navigation-discord:focus > a:focus,
.footer-navigation-discord:active > a:active, .footer-navigation-discord > a:focus,
.footer-navigation-discord > a:active, .footer-navigation-discord > a:hover {
  border-bottom: none !important;
  color:#7289da!important;
}

.footer-navigation-youtube:hover > a, .footer-navigation-youtube:focus > a:focus,
.footer-navigation-youtube:active > a:active, .footer-navigation-youtube > a:focus,
.footer-navigation-youtube > a:active, .footer-navigation-youtube > a:hover {
  border-bottom: none !important;
  color:#b00!important;
}

.footer-navigation-contact:hover > a, .footer-navigation-contact:focus > a:focus, 
.footer-navigation-contact:active > a:active, .footer-navigation-contact > a:focus, 
.footer-navigation-contact > a:active, .footer-navigation-contact > a:hover {
  border-bottom: none !important;
  color:rgb(25, 216, 152)!important;
  text-decoration: none !important;
}

footer ul {
  list-style-type: none;
  padding-left: 5%;
  margin-bottom: 10px;
  line-height: 1.7;
}

footer h5 {
  font-size: 20px;
  color: white;
  font-weight: bold;
  margin-top: 10px;
  margin-bottom: 10px;
  padding-left: 5%;
  text-shadow: 0 0 10px rgba(0,0,0,.9);
}

footer a {
  color: #d1d1d1;
  text-decoration: none;
}

.col-bordered {
  border-left: 2px solid #191919;
  border-right: 2px solid #191919;
}

.footer-copyright p {
  margin-top: 10px;
  margin-bottom: 20px;
  color: #ccc;
  text-align: center;
}

#image img {
  width: 40%;
  height: 40%;
  margin-top: 0px;
	margin-left: auto;
	margin-right: auto;
	display: block;
}

#image {
  position: absolute; 
  width: 100%; 
}

@media screen and (max-width: 991px) {
  footer {
    text-align: center;
  }
  .col-bordered {
    border-left: none;
    border-right: none;
  }
}


