.rankings {
  border: 1px solid #2d2e2f;
	padding: 0;
  margin: 0;
  margin-bottom: 20px;
  background-color: #2d2e2f99;
  -webkit-box-shadow: 0 0 20px rgba(0,0,0,0.7);
          box-shadow: 0 0 20px rgba(0,0,0,0.7);
}

.rankings > h2 {
  font-size: 20px;
  color: #fff;
  margin: 20px;
  text-shadow: 0 0 10px rgba(0,0,0,.9);
}

.rankings-body > h3 {
  font-size: 20px;
  color: #fff;
  margin-top: 0px;
  text-shadow: 0 0 10px rgba(0,0,0,.9);
}

.rankings-body {
  padding: 20px;
  border-top: 1px solid #5b5;
  border-bottom: 1px solid #5b5;
}

.rankings-item {
  font-size: 15px;
  padding-top: 5px;
  padding-bottom: 5px;
  text-shadow: 0 0 5px rgba(0,0,0,.9);
}

.rank {
  float: right;
}

.legendary {color: 	#FF8000;}

.epic {color: #A335EE;}

.blue {color: #0070DD;}

.rankings-footer > .btn, .rankings-footer > .btn:active, .rankings-footer > .btn:focus {
  color: #fff!important;
  border-radius: 0 !important;
  background-color: #2d2e2f !important;
  text-shadow: 0 0 10px rgba(0,0,0,.7);
}

.rankings-footer > .btn:hover {
  color: #fff!important;
  border-radius: 0 !important;
  background-color: #5b5 !important;
  text-shadow: 0 0 10px rgba(0,0,0,.7);
}


